import { Button } from "@mui/material";
import styled from "styled-components";

const MainButton = styled(Button)`
  color: #fff !important;
  padding: 13px 0;
  border-radius: 20px !important;
  width: 100%;
  min-width: 280px;
  max-width: 300px;
  height: 50px;
`;

export default MainButton;
